import { observer } from "mobx-react-lite";
import React from "react";
import { Button, Col, Container, Row } from "react-bootstrap";
import { useStore } from "../../App/Store/store";
import CardMovie from "../Card/CardMovie";

export default observer(function LeftHome() {
  const {
    movieStore: { moviesArray, pagination },
  } = useStore();

  return (
    <Container>
      <Row className="row-filter">
        <Col xl={6}>
          <h1 className="title">Phim mới</h1>
        </Col>
        <Col xl={6} className="m-auto">
          <Button variant="light ml-2 btn-filter-film active">Tất cả phim ({pagination!.totalItems})</Button>
          <Button variant="dark ml-2 btn-filter-film">Sắp chiếu</Button>
          <Button variant="dark ml-2 btn-filter-film">Phim mới</Button>
        </Col>
      </Row>
      <Row className="cards-film mt-3">
        {moviesArray.map((movie) => (
          <Col key={movie.id} md={3}>
            <CardMovie movie={movie} />
          </Col>
        ))}
      </Row>
    </Container>
  );
});
