/* eslint-disable jsx-a11y/iframe-has-title */
import { observer } from "mobx-react-lite";
import React, { useEffect, useState } from "react";
import { Col, Row } from "react-bootstrap";
import InfiniteScroll from "react-infinite-scroller";
import { PagingParams } from "../../App/Models/pagination";
import { useStore } from "../../App/Store/store";
import "./Home.css";
import LeftHome from "./LeftHome";

export default observer(function Home() {
  const {
    movieStore: { movieRegistry, loadMovies, loadingInitial, setPagingParams, pagination },
  } = useStore();

  const [loadingNext, setLoadingNext] = useState(false);

  function handleGetNext() {
    setLoadingNext(true);
    setPagingParams(new PagingParams(pagination!.currentPage + 1));
    loadMovies().then(() => setLoadingNext(false));
  }

  useEffect(() => {
    if (movieRegistry.size <= 1) loadMovies();
  }, [movieRegistry.size, loadMovies]);
  if (loadingInitial && !loadingNext) return null;
  return (
    <div className="container">
      <Row className="">
        <Col xs={9} className="left-side">
          <InfiniteScroll
            pageStart={0}
            loadMore={handleGetNext}
            hasMore={!loadingNext && !!pagination && pagination.currentPage < pagination.totalPages}
            initialLoad={false}
          >
            <LeftHome />
          </InfiniteScroll>
          <div
            style={{
              textAlign: "center",
              color: "#fff",
              fontSize: 50,
              marginTop: -10,
              display: pagination!.currentPage === pagination!.totalPages ? "none" : "block",
            }}
          >
            Continue...
          </div>
        </Col>
        <Col xs={3} className="hint-1">
          3 of 3
        </Col>
      </Row>
    </div>
  );
});
