import { makeAutoObservable, reaction, runInAction } from "mobx";
import agent from "../api/agent";
import { Movie } from "../Models/movie";
import { Pagination, PagingParams } from "../Models/pagination";

export default class MovieStore {
  movieRegistry = new Map<string, Movie>();
  selectedMovie: Movie | undefined = undefined;
  loading = false;
  loadingInitial = true;
  pagination: Pagination | null = null;
  pagingParams = new PagingParams();
  predicate = new Map().set("category", "all");

  get moviesArray() {
    return Array.from(this.movieRegistry.values());
  }

  constructor() {
    makeAutoObservable(this);
    reaction(
      () => this.predicate.keys(),
      () => {
        this.pagingParams = new PagingParams();
        this.movieRegistry.clear();
        this.loadMovies();
      }
    );
  }
  setPagingParams = (pagingParams: PagingParams) => {
    this.pagingParams = pagingParams;
  };
  get axiosParams() {
    const params = new URLSearchParams();
    params.append("pageNumber", this.pagingParams.pageNumber.toString());
    params.append("pageSize", this.pagingParams.pageSize.toString());
    this.predicate.forEach((value, key) => {
      params.append(key, value);
    });
    return params;
  }

  setPredicate = (predicate: string, value: string) => {
    // const resetPredicate = () => {
    //   this.predicate.forEach((value, key) => {
    //     this.predicate.delete(key);
    //   });
    // };
    switch (predicate) {
      case "category":
        this.predicate.delete("category");
        this.predicate.set("category", value);
        break;
    }
  };

  setPagination = (pagination: Pagination) => {
    this.pagination = pagination;
  };

  loadMovies = async () => {
    this.loadingInitial = true;
    try {
      const result = await agent.Movies.list(this.axiosParams);
      runInAction(() => {
        result.data.forEach((movie) => {
          this.setMovie(movie);
        });
        this.setPagination(result.pagination);
        this.loadingInitial = false;
      });
    } catch (error) {
      console.log(error);
      runInAction(() => {
        this.loadingInitial = false;
      });
    }
  };

  loadMovie = async (id: string) => {
    let movie = this.getMovie(id);
    if (movie) {
      this.selectedMovie = movie;
      return movie;
    } else {
      this.loadingInitial = true;
      try {
        movie = await agent.Movies.detail(id);
        this.setMovie(movie);
        runInAction(() => {
          this.selectedMovie = movie;
          this.loadingInitial = false;
        });
        return movie;
      } catch (error) {
        console.log(error);
        runInAction(() => {
          this.loadingInitial = false;
        });
      }
    }
  };

  private getMovie(id: string) {
    return this.movieRegistry.get(id);
  }

  setMovie(movie: Movie) {
    this.movieRegistry.set(movie.id, movie);
  }

  clearSelectedMovie = () => {
    this.selectedMovie = undefined;
  };
}
