/* eslint-disable jsx-a11y/iframe-has-title */
import { observer } from "mobx-react-lite";
import React, { useEffect } from "react";
import { Col, Container, Row } from "react-bootstrap";
import { useParams } from "react-router";
import { useStore } from "../../App/Store/store";
import "./Detail.css";

export default observer(function Detail() {
  const {
    movieStore: { loadMovie, clearSelectedMovie, selectedMovie: movie },
  } = useStore();
  const { id } = useParams<{ id: string }>();
  useEffect(() => {
    if (id) loadMovie(id);
    window.scrollTo(0, 0);
    return () => clearSelectedMovie();
  }, [id, loadMovie, clearSelectedMovie]);
  console.log(movie);
  if (!movie) return null;
  return (
    <Container className="">
      <Row className="header-content-block">
        <Col lg={5}>
          <div
            className="movie__image"
            style={{ backgroundImage: `url(https://www.fullphim.net${movie.image})` }}
          ></div>
        </Col>
        <Col lg={7}>
          <h1 className="title__movie">{movie.title}</h1>
          <div className="info-wrapper">
            <div className="preview__film">
              <div className="time">
                <h6 className="preview__label">Thời lượng:</h6>
                <h6 className="preview__content">{movie.time}</h6>
              </div>
              <div className="director">
                <h6 className="preview__label">Đạo diễn:</h6>
                <h6 className="preview__content">{movie.director}</h6>
              </div>
              <div>
                <i style={{ color: "yellow" }} className="far fa-clock"></i> {movie.publishYear}
              </div>
            </div>
            <div className="review__film">
              <p>{movie.description}</p>
            </div>
          </div>
        </Col>
      </Row>
      <Row>
        <div style={{ width: "100%", height: 0, position: "relative", paddingBottom: "56.250%", margin: 50 }}>
          <iframe
            allow="autoplay"
            src={`${movie.url}`}
            style={{ width: "100%", height: "100%", position: "absolute", left: 0, top: 0, overflow: "hidden" }}
            frameBorder={0}
            allowFullScreen={true}
          ></iframe>
        </div>
      </Row>
    </Container>
  );
});
